import React, { Component } from 'react'

export default class Todoinput extends Component {
    constructor(){
        super();

    }
    handleInput = (e)=>{
        if(e.keyCode === 13){
            this.props.addTodo(e.target.value);
            e.target.value = ''
        }
    }
   
    render() {
        return (
            <div id="header">
            <div id="header-content">
              <div id="header-title">ToDoList</div>
              <input type="text" id="header-inp" onKeyDown={(e)=>this.handleInput(e)} placeholder="添加ToDo" required="required" ></input>
          </div>
      </div>
        )
    }
}